/* PROGRESS LOADING */

.loader-60devs {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  width: 100%;
  z-index: 10000;
  opacity: 1;
  -webkit-transition: opacity 250ms 250ms;
  -o-transition: opacity 250ms 250ms;
  transition: opacity 250ms 250ms; }

.loader-60devs .loader-60devs-progress {
  height: 2px;
  width: 100%;
  background-color: #039be5;
  -webkit-transition-property: opacity, -webkit-transform;
  transition-property: opacity, -webkit-transform;
  -o-transition-property: transform, opacity;
  transition-property: transform, opacity;
  transition-property: transform, opacity, -webkit-transform;
  -webkit-transition-duration: 12s;
       -o-transition-duration: 12s;
          transition-duration: 12s;
  -webkit-transition-timing-function: cubic-bezier(0.04, 0.9, 0.11, 0.9);
       -o-transition-timing-function: cubic-bezier(0.04, 0.9, 0.11, 0.9);
          transition-timing-function: cubic-bezier(0.04, 0.9, 0.11, 0.9);
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%); }

.loader-60devs[data-state="hidden"] {
  display: none;
  -webkit-transition: none;
  -o-transition: none;
  transition: none; }

.loader-60devs[data-state="hidden"] .loader-60devs-progress {
  -webkit-transition: none;
  -o-transition: none;
  transition: none; }

.loader-60devs[data-state="running"]::before {
  content: '';
  position: fixed;
  z-index: 99999999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.loader-60devs[data-state="running"] .loader-60devs-progress {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0); }

.loader-60devs[data-state="finishing"] {
  opacity: 0; }

.loader-60devs[data-state="finishing"] .loader-60devs-progress {
  -webkit-transform: scaleX(100) translateX(0);
      -ms-transform: scaleX(100) translateX(0);
          transform: scaleX(100) translateX(0); }

/* MODAL COMMON */

.loading-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  display: inline-block;
  background: rgba(0, 0, 0, 0.5); }

.loading-inner {
  margin: auto;
  display: inline-block;
  position: relative;
  top: calc(50% - 64px);
  left: calc(50% - 46px); }

